<template>
  <div class="sticky-top">
    <div class="nav-content">
      <div class="container d-flex justify-content-between">
        <div class="nav-open-btn"
            :class="[isOpenNav? 'nav-close-btn': '']"
            @click="isShowNav">
          <span></span>
          <span></span>
          <span></span>
        </div>
        <div class="logo bg-setting"></div>
        <div class="list-content" :style="isOpenNav? showList : ''">
          <ul class="list">
            <li @click="isShowNav">
              <router-link to="/" class="list-item">首頁</router-link>
            </li>
            <li @click="isShowNav">
              <router-link to="/product/automation" class="list-item">自動化設備</router-link>
            </li>
            <li @click="isShowNav">
              <router-link to="/product/hydropower" class="list-item">水力發電設備</router-link>
            </li>
            <li @click="isShowNav">
              <router-link to="/product/vibrationTransport" class="list-item">震動運輸設備</router-link>
            </li>
            <li @click="isShowNav">
              <router-link to="/contact" class="list-item">聯絡我們</router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Navbar',
  data(){
    return{
      isOpenNav: false,
      deviceResolution: 0,
      showList: {
        right: '0px'
      },
    }
  },
  methods:{
    isShowNav(){
      this.isOpenNav = !this.isOpenNav;
    },
  }
}
</script>
<style lang="scss">
@import '../assets/scss/all';

.nav-content{
  width: 100%;
  height: 80px;
  background-color: #fff;
  box-shadow: 0 10px 20px -10px rgba(0,0,0,0.2);
  @include screen($mobile){
    height: 50px;
  }
}
.nav-open-btn{
  display: none;
  @include screen($mobile){
    display: block;
    width: 50px;
    height: 50px;
    position: absolute;
    right: 10px;
    padding: 10px 8px;
    span{
      display: block;
      width: 100%;
      height: 5px;
      background-color: gray;
      margin-bottom: calc((30px - 15px) / 2);
      transition-duration: .3s;
      transition-timing-function: cubic-bezier(0.43, 0.01, 0.61, 1.01);
      &:last-child{
        margin-bottom: 0;
        opacity: 1;
      }
    }
  }
}
.nav-close-btn{
  display: none;
  @include screen($mobile){
    display: block;
    width: 50px;
    height: 50px;
    position: absolute;
    right: 10px;
    padding: 5px 8px;
    span{
      display: block;
      height: 5px;
      background-color: gray;
      position: absolute;
      width: 40px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(225deg);
      &:nth-child(2){
        transform: translate(-50%, -50%) rotate(135deg);
      }
      &:nth-child(3) {
        opacity: 0;
      }
    }
  }
}
.logo{
  width: 150px;
  height: 80px;
  background-image: url('../assets/ATSC-logo_new.png');
  background-size: contain !important;
  @include screen($mobile){
    width: 100px;
    height: 50px;
    margin: 0 auto;
  }
}
.list-content{
  height: 80px;
  background-color: white;
  @include screen($mobile){
    transition-duration: .3s;
    margin-top: 50px;
    position: absolute;
    top:0;
    right: 100%;
    width: 100vw;
    height: 100vh;
    z-index: 99;
  }
}
.list{
  padding: 0;
  margin: 0;
  list-style: none;
  li{
    padding: 31px 15px;
    float: left;
    .list-item{
      display: block;
      font-size: 1.125rem; //18px
      line-height: 1;
      text-align: center;
      color: black;
      text-decoration: none;
      &.router-link-exact-active::after{
        @include screen($mobile){
          content: '';
          display: block;
          padding-bottom: 10px;
          border-bottom: 3px solid #14213D;
          width: 50%;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
    @include screen($mobile){
      padding: 20px;
      float: none;
    }
  }
}
</style>